import { useEffect } from 'react';
import { autoJsStoreUtils, isRealInAndroid } from '@droid/android';
import { PrivacyDialogDisplay } from './PrivacyDialogDisplay';
import { useState } from 'react';
import { IDispatcher } from '@fanfanlo';

interface PrivacyDialogAutoProps {
  type: string;
  area: string;
  dispatcher?:IDispatcher
}
export const privacyDialogClosedEventType = "privacyDialogClosed"
interface IPrivacyInfo {
  agree: boolean;
  disagreeTime: number;
}

let autoOpened = false; // 用于标记是否已经自动弹出过隐私协议
/**
 * 自动逻辑组件 - 负责处理隐私协议的自动弹出逻辑
 * 包含原有的35-49行逻辑：检查用户是否已同意、是否在冷却期等
 */
export function PrivacyDialogAuto({ type, area, dispatcher }: PrivacyDialogAutoProps) {
  const [open, setOpen] = useState(false);

  const handleClose = () => {
    autoOpened = true; // 设置为已自动弹出
    setOpen(false);
    dispatcher?.dispatch('privacyDialogClosed');
  };

  // 原有的自动弹出逻辑 (35-49行)
  useEffect(() => {
    // web中也可以调试
    const def: IPrivacyInfo = { agree: isRealInAndroid ? false : true, disagreeTime: 0 };
    const res = autoJsStoreUtils.read<IPrivacyInfo>(`privacy_${type}`, def);
    const value: IPrivacyInfo = res && 'agree' in res ? res : def;
    if(autoOpened){
      //自动检测已经弹出过了就不再弹了
      return;
    }
    // 不在Android环境中不弹出
    // if (!isRealInAndroid) {
    //   return;
    // }
    
    // 已经同意了，不弹出
    if (value.agree) {
      return;
    }
    
    // 在冷却期内（拒绝后3天内），不弹出
    if (value.disagreeTime && value.disagreeTime != 0 && value.disagreeTime + 3 * 24 * 60 * 60 * 1000 > Date.now()) {
      return;
    }
    
    // 满足条件，弹出隐私协议
    setOpen(true);
  }, [type]);

  return (
    <PrivacyDialogDisplay
      open={open }
      onClose={handleClose}
      type={type}
      area={area}
    />
  );
}